.react-calendar {width:700px; vertical-align:130px; max-width:100%; background:transparent; border:none; font-family:Arial,Helvetica,sans-serif; line-height:1.125em;}
.react-calendar--doubleView {width:700px;}
.react-calendar--doubleView .react-calendar__viewContainer {display:flex; margin:-0.5em;}
.react-calendar--doubleView .react-calendar__viewContainer > * {width:50%; margin:0.5em;}
.react-calendar,.react-calendar *,.react-calendar *:after,.react-calendar *:before {-moz-box-sizing:border-box;-webkit-box-sizing:border-box; box-sizing:border-box; font-size:1.5vw;}
.react-calendar button {margin:0; border:0; outline:none; color:#fff;}
.react-calendar button:enabled:hover {cursor:pointer;}
.react-calendar__navigation {display:flex; height:44px; margin-bottom:2em;}
.react-calendar__navigation button {min-width:44px; background:none; color:#cf1056; font-size:40px; font-weight:bold;}
.react-calendar__navigation button:enabled:focus,.react-calendar__navigation button:enabled:hover {background-color:#1c266f; border-radius:50%;}
.react-calendar__navigation button[disabled] {background-color:#1c266f;}
.react-calendar__month-view__weekdays {text-align:center; text-transform:uppercase; font-weight:bold; font-size:0.75em; display:none!important;}
.react-calendar__month-view__weekdays__weekday {padding:2.5em;}
.react-calendar__month-view__weekNumbers .react-calendar__tile {display:flex; align-items:center; justify-content:center; font-size:0.75em; font-weight:bold; padding:calc(0.75em / 0.75) calc(0.5em / 0.75);}
.react-calendar__month-view__days__day--weekend {color:#fff;}
.react-calendar__month-view__days__day--neighboringMonth {color:#757575!important;}
.react-calendar__century-view .react-calendar__tile,.react-calendar__decade-view .react-calendar__tile,.react-calendar__year-view .react-calendar__tile {padding:2em 0.5em;}
.react-calendar__tile {max-width:100%; text-align:center; padding:0.75em 0.5em; background:none;}
.react-calendar__tile:disabled {background-color:#f0f0f0;}
.react-calendar__tile:enabled:focus,.react-calendar__tile:enabled:hover {background-color:#1c266f; border-radius:50%;}
.react-calendar__tile--now {background:#a1155a; border-radius:50%; background-image:linear-gradient(to left, #d31056 0, #192267  95%);}
.react-calendar__tile--now:enabled:focus,.react-calendar__tile--now:enabled:hover {background:#1c266f;}
.react-calendar__tile--hasActive {background:#76baff;}
.react-calendar__tile--hasActive:enabled:focus,.react-calendar__tile--hasActive:enabled:hover {background:#a9d4ff;}
.react-calendar__tile--active {background-color:#1c266f;}
.react-calendar__tile--active:enabled:focus,.react-calendar__tile--active:enabled:hover {background:#a1155a; border-radius:50%; background-image:linear-gradient(to right, #d31056 10%, #192267  20%);}
.react-calendar--selectRange .react-calendar__tile--hover {background-color:#e6e6e6;}

@media (max-width:768px) {
.react-calendar__navigation button {font-size:20px;}
.react-calendar,.react-calendar *,.react-calendar *:after,.react-calendar *:before {font-size:13px;}



























